<template>
  <el-row class="tac">
    <el-col :span="24">
      <el-menu :default-active="activeNav" class="el-menu" @open="handleOpen" @close="handleClose" 
      unique-opened
      router
      background-color="#00577E"
      text-color="#fff" 
      active-text-color="#fff"
      >
      	<div id="div_logo">
      		<img src="/maia_logo.fcb6549e.png" id="icon_text">
          <el-dropdown trigger="click" @command="handleCommand">
            <a><img src="@/assets/icon/icon_profile.png" id="icon_more"></a>
              <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="lo">LOG OUT</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
      	</div>
      	<el-menu-item index="/Dashboard/Overview">
        <span slot="title" class="nav_span"><b>Overview</b></span>
      	</el-menu-item>
      	<div id="div_client">
      		<span id="clients">CLIENTS</span>
        </div>
          <el-menu-item v-for="item in category" class="nav-name" :index="item.componentName" v-text="item.name" @click='setPatient(item)' route>
          </el-menu-item>
        </el-menu-item-group>
      </el-submenu>
      </el-menu>
    </el-col>
  </el-row>
</template>

<script>
  import axios from 'axios'
  var a = ''
  export default {
    data () {
      return {
        activeNav: '/Overview',
        patient: '',
        category:[],
        raw: '',
      }
    },
    methods: {
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      },
      handleCommand(command) {
        if (command == "lo") {
          this.quit();
        }
      },
      setPatient(e) {
        this.$store.dispatch('setPatient_idData', e.uidPatient)
        this.$store.dispatch('setPatient_nameData', e.name)
        console.log(this.$store.state.patient_id)
      },
      quit() {
        this.$store.dispatch('setUserData', '')
        this.$router.push({path: '/login'})
      },
    },
    filters: {
      
    },
    created() {
      const index = window.location.href.split('/').length
      this.activeNav = window.location.href.split('/')[index-1]
      console.log(this.activeNav)
    },
    mounted() {
      var index = 1
      var get_path ='http://35.197.160.94:8094/doctor/overview?uid_doctor=' + this.$store.state.user
      axios.get(get_path)
      .then(response=>{
          this.raw=response.data;
          this.raw.forEach(el => {
            const _name = el.firstName+' '+el.lastName
            const _cate = this.category.find(e => e.name === _name)
            if (!_cate) {
              this.category.push({
                name: _name,
                componentName: '/Dashboard/Detail/trends',
                uidPatient:el.uid,
              })
            }
            index++;
          })
          // console.log(this.category)
      })
      .catch(error=>{
          console.log(error);
          this.$message({
            message: "Network Error",
            type: "error"
          })
      })

    },
    watch: {
      '$route' (to, from) {
        const index = window.location.href.split('/').length
        this.activeNav = window.location.href.split('/')[index-1]
        console.log(this.activeNav)
      }
    }
  }
  
</script>

